:root {
  --primary-color: #3498db;
  --secondary-color: #2c3e50;
  --background-color: #ecf0f1;
  --text-color: #34495e;
  --border-color: #bdc3c7;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.app-header {
  text-align: center;
  margin-bottom: 2rem;
}

h1 {
  color: var(--primary-color);
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.description {
  font-size: 1rem;
  color: var(--secondary-color);
}

.lookup-form {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.address-input {
  width: 60%;
  padding: 0.75rem;
  font-size: 1rem;
  border: 1px solid var(--border-color);
  border-radius: 4px 0 0 4px;
}

.lookup-button {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  transition: background-color 0.3s;
}

.lookup-button:hover {
  background-color: #2980b9;
}

.lookup-button:disabled {
  background-color: var(--border-color);
  cursor: not-allowed;
}

.loader, .error {
  text-align: center;
  margin-bottom: 1rem;
}

.error {
  color: #e74c3c;
}

.results-container {
  background-color: white;
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h2 {
  color: var(--secondary-color);
  margin-bottom: 1rem;
}

.rewards-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 2rem;
}

.rewards-table th,
.rewards-table td {
  padding: 0.75rem;
  text-align: left;
  border-bottom: 1px solid var(--border-color);
}

.rewards-table th {
  background-color: var(--primary-color);
  color: white;
}

.generate-button {
  display: block;
  width: 100%;
  padding: 1rem;
  font-size: 1rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-bottom: 1rem;
}

.generate-button:hover {
  background-color: #2980b9;
}

.safe-transaction-data {
  background-color: var(--secondary-color);
  color: white;
  padding: 1rem;
  border-radius: 4px;
  margin-top: 1rem;
}

.safe-transaction-data pre {
  white-space: pre-wrap;
  word-break: break-all;
}

.copy-button {
  display: block;
  width: 100%;
  padding: 0.5rem;
  font-size: 0.9rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 1rem;
}

.copy-button:hover {
  background-color: #2980b9;
}
